import React from 'react'
import * as d3 from 'd3'
import { format, HierarchyCircularNode } from 'd3'
import { Playmate } from '../types'

type Data = (HierarchyCircularNode<Playmate> & {
  cx: number
  cy: number
  stroke: string
})[]

const GroupingCircles = ({
  data,
  transitionDuration,
  ...rest
}: {
  data: Data
  transitionDuration: number
} & { [key: string]: any }) => {
  const ref = React.useRef<SVGGElement>(null)

  const total = data.reduce((sum, cur) => sum + cur.children.length, 0)

  React.useEffect(() => {
    if (!ref.current) return

    const t = d3.transition().duration(transitionDuration)

    d3.select(ref.current) //
      .selectAll('circle')
      .data(data)
      .join(
        enter =>
          enter
            .append('circle')
            .attr('cx', d => d.cx)
            .attr('cy', d => d.cy)
            .attr('r', 0)
            .attr('opacity', 1)
            .attr('stroke', d => d.stroke)
            .attr('stroke-width', 3)
            .attr('fill', 'transparent')
            .call(enter =>
              enter //
                .transition(t)
                .attr('r', d => d.r),
            ),
        update =>
          update.call(update =>
            update
              // @ts-ignore
              .transition(t)
              .attr('cx', d => d.cx)
              .attr('cy', d => d.cy)
              .attr('r', d => d.r)
              .attr('stroke', d => d.stroke),
          ),
        exit =>
          exit.call(exit =>
            exit
              // @ts-ignore
              .transition(t)
              .attr('opacity', 0)
              .remove(),
          ),
      )

    const arc = d3
      .arc<Data[number]>()
      .innerRadius(d => +d.r - 2)
      .outerRadius(d => +d.r + 16)
      .startAngle(0)
      .endAngle(d => {
        const total =
          (d.data[0]
            ?.replace('Latin', 'Latino')
            ?.replace('Real/Natural', 'Natural')
            ?.replace('Fake/Enhanced', 'Enhanced')?.length ?? 2) + 4
        const angl = (total * 10) / (d.r + Math.PI * 6)
        return angl
      })

    d3.select(ref.current)
      .selectAll('path')
      .data(data)
      .join(
        enter =>
          enter
            .append('path')
            .attr('d', arc)
            .attr('fill', d => d.stroke)
            .attr('fill-opacity', 0.4)
            .attr('stroke', d => d.stroke)
            .attr('stroke-opacity', 0.4)
            .attr('transform', d => `translate(${d.cx},${d.cy})`)
            .attr('opacity', 0)
            .attr('id', d => `arc-${d.data[0]}`)
            .call(enter =>
              enter //
                .transition()
                .delay(transitionDuration)
                .duration(transitionDuration)
                .attr('opacity', 1),
            ),
        update =>
          update.call(update =>
            update
              .attr('opacity', 0)
              .attr('d', arc)
              .attr('transform', d => `translate(${d.cx},${d.cy})`)
              .attr('stroke', d => d.stroke)
              .attr('fill', d => d.stroke)
              .attr('id', d => `arc-${d.data[0]}`)
              .transition()
              .delay(transitionDuration)
              .duration(transitionDuration)
              .attr('opacity', 1),
          ),
        exit =>
          exit.call(exit => exit.transition(t).attr('opacity', 0).remove()),
      )

    d3.select(ref.current)
      .selectAll('text')
      .data(data)
      .join(
        enter => {
          return enter
            .append('text')
            .attr('dy', 0)
            .attr('dx', 4)
            .attr('opacity', 0)
            .call(enter =>
              enter //
                .transition()
                .delay(transitionDuration)
                .duration(transitionDuration)
                .attr('opacity', 1),
            )
            .append('textPath')
            .attr('xlink:href', d => `#arc-${d.data[0]}`)
            .attr('dominant-baseline', 'hanging')
            .attr('font-size', 14)
            .attr('fill', 'white')
            .attr('letter-spacing', 2)
            .text(
              d =>
                `${
                  d.data[0]
                    ?.replace('Latin', 'Latino')
                    ?.replace('Real/Natural', 'Natural')
                    ?.replace('Fake/Enhanced', 'Enhanced') ?? '??'
                } ${format('.0%')(d.children.length / total)}`,
            )
            .attr('id', d => `label-${d.data[0]}`)
            .attr('data-length', function () {
              return this.getComputedTextLength()
            })
            .attr('spacing', 'auto')
        },
        update => {
          update
            .attr('opacity', 0)
            .transition()
            .delay(transitionDuration)
            .duration(transitionDuration)
            .attr('opacity', 1)
            .select('textPath')
            .attr('xlink:href', d => {
              return `#arc-${d.data[0]}`
            })
            .text(
              d =>
                `${
                  d.data[0]
                    ?.replace('Latin', 'Latino')
                    ?.replace('Real/Natural', 'Natural')
                    ?.replace('Fake/Enhanced', 'Enhanced') ?? '??'
                } ${format('.0%')(d.children.length / total)}`,
            )
            .attr('id', d => `label-${d.data[0]}`)

          return update
        },
      )
  })

  return <g ref={ref} {...rest} />
}

export default GroupingCircles
